import './index.scss';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faFolder,faFileText,faCommentDots} from '@fortawesome/free-solid-svg-icons'
import React, { useState } from "react";
import Modal from "../Modal";
import cs50 from "../../assets/images/certs/cs50t.jpg"
import elements from "../../assets/images/certs/element_of_ai.png"
import huawei from "../../assets/images/certs/huawei.jpg"
import pgdata from "../../assets/images/certs/pgdata.jpg"
import pginnovation from "../../assets/images/certs/pginnovation.jpg"
import pgleader from "../../assets/images/certs/pgleader.jpg"
import pgproj from "../../assets/images/certs/pgproj.jpg"
import redhat from "../../assets/images/certs/redhat.jpg"
import unix from "../../assets/images/certs/unix_workbench.jpg"
import { ReactTerminal } from "react-terminal";

const Container = () =>{

    const [isOpen, setIsOpen] = useState(false);
    const [vals,setVals] = useState('');
    const [file,setFile] = useState('');
    const [head,setHead] = useState('');
    return(
      
    <div className="folder-package">

        <button className='folder' onClick={() => {setIsOpen(true);setVals(certs);setFile("Certificates");setHead('')}}>
        <FontAwesomeIcon icon={faFolder} color="#4d4d4e" />
        <a>Certificates</a>
        </button>



        
        <button className='folder' onClick={() => {setIsOpen(true);setVals(aboutme);setFile("aboutme.txt");setHead("About Me")}}>
        <FontAwesomeIcon icon={faFileText} color="#4d4d4e" />
        <a>aboutme.txt</a>
        </button>
        {isOpen && <Modal setIsOpen={setIsOpen} val={vals} fileName={file} header={head} />}
            
            
        <button className='folder' onClick={() => {setIsOpen(true);setVals(embed);setFile("Llama2 Chatbot");setHead("")}}>
        <FontAwesomeIcon icon={faCommentDots} color="#4d4d4e" />
        <a>Chatbot.exe</a>
        </button>    
            
            </div>)

        

}

const embedStyle = {
  width:'100%',
  border:'none',
  height:'100%'
}

const embed = 
<iframe
  src="https://djordje34-llama2.streamlit.app/?embed=true"
  style={embedStyle}
></iframe>;

const aboutme =  
<textarea className='notepad'>Hello, and welcome to my website! My name is Djordje, 
and I'm a student based in Kragujevac, Serbia. 
My passion lies in machine learning and data science, 
and I'm determined to pursue a career in these fields 
as a machine language engineer or data scientist.


I love what I do, and I'm constantly striving to 
learn and improve my skills. I believe that through 
hard work, dedication, and a passion for learning, 
anything is possible. I'm excited about the possibilities
 that lie ahead, and I'm committed to excelling in my 
 chosen field.


In my free time, I enjoy exploring new technologies, 
participating in coding challenges, and staying 
up-to-date with the latest trends and developments in the
 industry.


I believe that the key to success is staying curious 
and always willing to learn.


Thank you for taking the time to visit my website. 
If you have any questions or would like to connect, 
please don't hesitate to reach out. 
I look forward to hearing from you!
    </textarea>;

const certs = <>
<div class="gallery">
<a target="_blank" href={cs50}>
  <img src={cs50} alt="cs50" width="600" height="400"/>
</a>
<div class="desc">cs50t.jpg</div>
</div>

<div class="gallery">
<a target="_blank" href={elements}>
  <img src={elements} alt="ai" width="600" height="400"/>
</a>
<div class="desc">elementsofai.jpg</div>
</div>

<div class="gallery">
<a target="_blank" href={huawei}>
  <img src={huawei} alt="huawei" width="600" height="400"/>
</a>
<div class="desc">hcia_ai.jpg</div>
</div>

<div class="gallery">
<a target="_blank" href={pgdata}>
  <img src={pgdata} alt="pgdata" width="600" height="400"/>
</a>
<div class="desc">pgdata.jpg</div>
</div>
<div class="gallery">
<a target="_blank" href={pginnovation}>
  <img src={pginnovation} alt="pginno" width="600" height="400"/>
</a>
<div class="desc">pginnovation.jpg</div>
</div>
<div class="gallery">
<a target="_blank" href={pgleader}>
  <img src={pgleader} alt="pgleader" width="600" height="400"/>
</a>
<div class="desc">pgleadership.jpg</div>
</div>

<div class="gallery">
<a target="_blank" href={redhat}>
  <img src={redhat} alt="redhat" width="600" height="400"/>
</a>
<div class="desc">redhat.jpg</div>
</div>

<div class="gallery">
<a target="_blank" href={unix}>
  <img src={unix} alt="unix" width="600" height="400"/>
</a>
<div class="desc">unix_workbench.jpg</div>
</div>
</>;

export default Container