import './index.scss';
import React, { useState,useEffect } from "react";
import {Link,NavLink } from 'react-router-dom'
import Logo from '../../assets/images/logo.png'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faHome,faUser,faTerminal,faEnvelope} from '@fortawesome/free-solid-svg-icons'
import { faGithub, faLinkedin } from '@fortawesome/free-brands-svg-icons';
import TerminalModal from '../TerminalModal';
import Modal from '../Modal';
import MailClient from '../MailClient';
const Sidebar = () => {
    const [onOpen, setOpen] = useState(false);
    const [onMail, setMail] = useState(false);
    return (
<div className='nav-bar'>
<Link className='logo' to='/djordje34'>
    <img src={Logo} alt="logo" />
</Link>
<nav>
    <NavLink exact="true" activeclassname="active" to="/djordje34">
    <FontAwesomeIcon icon={faHome} color="#4d4d4e" />
    </NavLink>

    <NavLink exact="true" activeclassname="active" className="contact-link" to="/djordje34"
    onClick={() => {setOpen(true);}}>
    <FontAwesomeIcon icon={faTerminal} color="#4d4d4e" />
    </NavLink>

    <NavLink exact="true" activeclassname="active" to="/djordje34" onClick={() => setMail(true)}>
    <FontAwesomeIcon icon={faEnvelope} color="#4d4d4e" />
    </NavLink>

    <NavLink exact="true" activeclassname="active" className="contact-link" to="https://github.com/djordje34">
    <FontAwesomeIcon icon={faGithub} color="#4d4d4e" />
    </NavLink>

    <NavLink exact="true" activeclassname="active" className="contact-link" to="https://www.linkedin.com/in/djordjekarisic/">
    <FontAwesomeIcon icon={faLinkedin} color="#4d4d4e" />
    </NavLink>
</nav>
{onOpen && <TerminalModal setOpen={setOpen} />}
{onMail && <Modal setIsOpen={setMail} val={<MailClient/>} fileName={"mailclient.exe"} header={"E-mail Client"} />}

</div>

)}

export default Sidebar