import './index.scss';

import React from 'react'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faWindowClose} from '@fortawesome/free-solid-svg-icons'
import Draggable, {DraggableCore} from 'react-draggable';

const Modal = ({setIsOpen,fileName,header,val}) =>(
    <Draggable positionOffset={{ x: '-50%', y: '-50%' }} handle="#handle2">

    <div className='darkBG centered modal handle'>
        <div className='modalHeader'>
        <button className='closeBtn' onClick={() => setIsOpen(false)}>
        <FontAwesomeIcon className='close' icon={faWindowClose} color="#4d4d4e" />
        </button>
        <div className='fileName' id="handle2">
                {fileName}
            </div>
        </div>
        <div className='contentHeader'>
            {header}
        </div>
        <div className='content'>
            {val} 

      </div>
    </div>
</Draggable>
)
export default Modal